.folder-structure {
  width: 100%;
  height: 100vh;
  display: flex;
  .sidebar {
    min-width: 15%;
    padding: 12px;
    border-right: 1px solid gray;
    resize: horizontal;
    overflow: scroll;
    .folder {
      display: flex;
      align-items: center;
      cursor: pointer;
      .title {
        font-size: 12px;
        padding-left: 8px;
      }
      .fas {
        font-size: 1.5em;
        color: #56a5e0
      }
    }
    .file {
      padding: 2px 0px 2px 12px;
      cursor: pointer;
      .title {
        font-size: 10px;
        padding-left: 8px;
      }
    }
  }
  .empty-folder {
    padding-left: 24px;
    font-size: 8px;
  }
  .content {
    padding: 12px;
  }
}
